<template>
  <div id="LiveTimeList">
    <head-top title="实况录播"></head-top>
    <van-pull-refresh v-model="loading" @refresh="getList" class="pull_fresh">
      <div v-if="dataList && dataList.length > 0" class="main">
        <div v-for="(item, index) in dataList" :key="index" class="line_mark" @click="select(item)">
          <van-icon name="video-o" class="line_icon" />
          <div>{{ item.mark }}</div>
        </div>
      </div>
      <r-lack-data v-else></r-lack-data>
    </van-pull-refresh>
  </div>
</template>

<script>
import defaultBg from "@/assets/video/video.png";
import { microLessonTypeName } from "@/utils/enum";

export default {
  name: "",
  components: {},
  props: {
    routeType: {
      type: String,
      default: "course"
    }
  },
  data() {
    return {
      selectIndex: -1,
      dataList: [],
      playerOptions: {
        autoplay: false,
        muted: true,
        language: "en",
        playbackRates: [0.7, 1.0, 1.5, 2.0],
        sources: [
          {
            type: "video/mp4",
            // mp4
            src: "http://vjs.zencdn.net/v/oceans.mp4"
            // webm
            // src: "https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm"
          }
        ],
        poster:
          "https://surmon-china.github.io/vue-quill-editor/static/images/surmon-1.jpg"
      },
      loading: false
    };
  },
  mounted() {},
  created() {
    this.$store.commit("SET_LIVELESSION", null);
    this.getList();
  },
  methods: {
    getList() {
      this.dataList = [];
      this.loading = true;
      let teacherTypes = [1, 2, 3, 4];
      if (this.$config.isTest) {
      } else {
        this.$mainHttp.miniCourse
          .getList({ ciID: this.$store.getters.courseID })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                let mark = item["RecMark"] + "";
                let sub = {
                  ...item,
                  title: item["MLTitle"],
                  time: item["StartTime"],
                  typeName: microLessonTypeName(item["MLType"]),
                  src:
                    this.routeType === "course"
                      ? this.$gatewayHttp.resourse.video(
                          item["MLID"],
                          item["FileFormat"]
                        )
                      : this.$mainHttp.resourse.video(
                          item["MLID"],
                          item["FileFormat"]
                        ),
                  poster: defaultBg
                };

                let index = this.dataList.findIndex(r => r.mark === mark);
                if (index > -1) {
                  if (teacherTypes.indexOf(item["MLType"]) > -1) {
                    this.dataList[index].teachers.push(sub);
                  } else {
                    this.dataList[index].students.push(sub);
                  }
                } else {
                  let k = teacherTypes.indexOf(item["MLType"]);
                  this.dataList.push({
                    mark,
                    teachers: k > -1 ? [sub] : [],
                    students: k > -1 ? [] : [sub]
                  });
                }
              });
            }
            this.loading = false;
          })
          .catch(err => {
            this.loading = false;
          });
      }
    },
    select(item) {
      this.$store.commit("SET_LIVELESSION", item);
      this.$router.push("/liveLessonType");
    }
  }
};
</script>

<style lang="less">
#LiveTimeList {
  width: 100%;
  height: 100%;
  background-color: #fff;

  .main {
    width: 100%;
    background: #ffffff;
    max-height: calc(100vh - 4.5rem);
    overflow-y: auto;
    .line_mark {
      position: relative;
      display: flex;
      justify-content: space-between;
      margin: 0 3vw;
      font-size: 1.25rem;
      line-height: 4.5rem;
      color: #777;
      &:after {
        content: "";
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 1px;
        border-top: 1px solid #f0f1f1;
        transform: scaleY(0.6);
      }

      .line_icon {
        font-size: 2.85rem;
        line-height: 4.5rem;
        padding-left: 1vw;
        padding-right: 2vw;
      }
    }
  }
}
</style>



